import React from 'react';
import { FC  } from 'react';
import './MyButton.css'

export interface MyButtonProps {
    primary: boolean,
    red: boolean,
    yellow: boolean,
    green: boolean,
    violet: boolean
    label: string,
    color: string,
    disabled?: boolean,
    hover?: boolean,
    focus?: boolean,
    outline?: boolean,
    normal?: boolean,
    size?: 'small' | 'medium' | 'large';
    onClick?: () => void;
}
const MyButton: FC<MyButtonProps> =
    ({
         color,
         ...props
    }) => {

    const rootClasses = ['my-button']

        if (props.primary) {
            rootClasses.push('primary')
        } else if (props.red) {
            rootClasses.push('red')
        } else if (props.yellow) {
            rootClasses.push('yellow')
        } else if (props.green) {
            rootClasses.push('green')
        } else if (props.violet) {
            rootClasses.push('violet')
        } else if (props.disabled) {
            rootClasses.push('disabled')
        }

    return (
        <button {...props} className={rootClasses.join(' ')} style={{color}}>
            { props.label }
        </button>
    )
}

export default MyButton
